import React, { useState, useEffect } from "react";
import RankingItem from "Components/RankingItem";
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
      width: "30em",
      "& > *": {
        padding: "0.4em"
      }
    },
  }));

function RankingList() {
    const [width, setWidth] = useState(window.innerWidth);
    useEffect(() => {
        const index = window.addEventListener("resize", () => setWidth(window.innerWidth));
        return () => window.removeEventListener("resize", index);
    }, []);

    const classes = useStyles();
    return (
        <div className={classes.root}>
            <div>{width}</div>
            <RankingItem ranking={1} name={"ber"} time={"12:45.0234"}/>
            <RankingItem ranking={2} name={"fre"} time={"12:45.0234"} accent={"red"} gap={3.454}/>
            <RankingItem ranking={3} name={"oll"} time={"12:45.0234"} brand="MercedesBenz"/>
            <RankingItem ranking={4} name={"ber"} time={"12:45.0234"} color={"#3e9200"} accent={"#a53518"}/>
            <RankingItem ranking={5} name={"prr"} time={"12:45.0234"} brand="MercedesBenz"/>
            <RankingItem ranking={6} name={"prr"} time={"12:45.0234"} brand="MercedesBenz"/>
            <RankingItem ranking={7} name={"prr"} time={"12:45.0234"} brand="MercedesBenz"/>
            <RankingItem ranking={8} name={"prr"} time={"12:45.0234"} brand="MercedesBenz"/>
            <RankingItem ranking={9} name={"prr"} time={"12:45.0234"} brand="MercedesBenz"/>
        </div>
    );
}

export default RankingList;