import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import Grid from "@material-ui/core/Grid";
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({
    root: {
        position: "relative",
        height: "100%"
    },
    textLayout: {
        top: 0,
        height: "100%",
        position: "absolute"
    },
    text: {
        fontSize: "2em",
        color: "white",
        paddingRight: "1.5em"
    }
});

function Ranking({ ranking, color = "#565459", accent = "#92870f" }) {
    const classes = useStyles();
    return (
        <div className={classes.root}>
            <svg height="100%" width="100%" viewBox="0 0 133.17249 62" preserveAspectRatio="none">
                <g
                    transform="translate(53.052959,0.77060652)"
                    id="layer1">
                    <path
                        id="path4500"
                        transform="matrix(0.26458333,0,0,0.26458333,7.0103529,-0.61009725)"
                        d="M -224.86914,1.5351562 V 231.85156 H 3.0976562 L 178.26562,1.5351562 Z"
                        style={{ fill: color }} />
                    <rect
                        transform="matrix(1,0,-0.6053656,0.79594754,0,0)"
                        y="-0.25594509"
                        x="54.021614"
                        height="76.559631"
                        width="25.123335"
                        id="rect4485"
                        style={{ fill: accent }} />
                </g>
            </svg>
            <Grid container className={classes.textLayout} alignItems="center" justify="center">
                <Typography className={classes.text}>
                    {ranking}
                </Typography>
            </Grid>
        </div>
    );
}

export default Ranking;