import React from "react";
import Audi from "./Audi.svg";
import MercedesBenz from "./MercedesBenz.svg";
import { makeStyles } from "@material-ui/core/styles";

const Logos = {
    Audi,
    MercedesBenz
}

const useStyle = makeStyles({
    img: {
        height: "10px",
        display: "block"
    }
});

function Logo({ brand = "Audi" }) {
    const classes = useStyle();
    return (
        <img src={Logos[brand]} className={classes.img}/>
    );
}

export default Logo;