import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Ranking from "Components/Ranking";
import Logo from "Components/Logo";
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({
    root: {
        backgroundColor: "grey",
        height: "3em"
    },
    fill: {
        height: "100%"
    },
    logo: {
        padding: "1em",
        height: "100%"
    }
});


function RankingItem({ ranking, color, accent, name, brand, time, gap, out }) {
    const classes = useStyles();
    return (
        <div>
            <Grid container className={classes.root} alignItems="center">
                <Grid item xs={2} className={classes.fill}>
                    <Ranking ranking={ranking} color={color} accent={accent} />
                </Grid>
                <Grid item xs={2}>
                    <Typography variant="overline" display="block">
                        {name}
                    </Typography>
                </Grid>
                <Grid item xs={2} container alignItems="center" justify="center" className={classes.logo} >
                    <Logo brand={brand}/>
                </Grid>
                <Grid item xs={3}>
                    <Typography variant="button" display="block">
                        {time}
                    </Typography>
                </Grid>
                <Grid item xs={2}>
                    {gap}
                </Grid>
                <Grid item xs={1}>
                    OUT
                </Grid>
            </Grid>
        </div>
    );
}

export default RankingItem;